@import "./src/sass/helpers/var";

.navigation {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;

  &__wrapper {
    display: flex;
    justify-self: flex-end;
    justify-content: space-between;
    width: 50%;
    align-items: center; }

  &__item {
    width: 100%;
    height: 100%;
    text-align: center;
    position: relative;
    &:hover,
    &:focus,
    &:active {
      & > .nav-submenu {
        transform: translate(-5%, 12.5%) scale(1);
        opacity: 1;
        visibility: visible; }

      & > ul {
        display: inherit; } } }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    width: 100%;
    padding: 0.75rem 1rem;
    font-size: 1vw;
    color: $lightGrey;

    &--color {
      color: $black; }

    &:after {
      content: '';
      display: block;
      width: 50%;
      height: 2px;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      transform: translateX(50%);
      background-color: $purple;
      visibility: hidden;
      opacity: 0; }

    &:hover,
    &:focus,
    &:active {
      color: $black;
      &:after {
        visibility: visible;
        opacity: 1; } } } }

.nav-submenu {
  display: flex;
  opacity: 0;
  visibility: hidden;
  transform-origin: bottom center;
  transform: translate(0, -100%) scale(0);
  position: absolute;
  top: 100%;
  left: 0;
  flex-direction: column;
  background-color: $white;
  box-shadow: inset 0 0 2.5px 1px rgba($purple, 0.5);
  padding: 1rem 0;
  z-index: 998;
  transition: all 1s ease;
  &__item {
    display: flex; }
  &__link {
    display: flex;
    font-weight: 400;
    width: 100%;
    font-size: 1vw;
    padding: 0.75rem 1.25rem;
    justify-content: flex-start;
    align-items: center;

    &:hover,
    &:active,
    &:focus {
      color: rgba($purple, 0.85); }

    &--color {
      color: $black; } } }

.logo {
  width: 15%;
  padding: 0.5rem 0;
  height: 3vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  svg {
    fill: rgba($purple, 0.75);
    height: 2rem; } }

.menu {
  display: none;
  position: relative;
  width: 3rem;
  height: 3rem;
  padding: 0.35rem 0.15rem;

  &:hover {
    cursor: pointer; }

  span {
    margin: 0 auto;
    top: 12px;
    position: relative;
    width: 100%;
    height: 6px;
    background-color: $black;
    display: block;
    transition: $transition;

    &:before,
    &:after {
      position: absolute;
      content: '';
      width: 100%;
      height: 6px;
      background-color: $black;
      display: block;
      transition: $transition; }

    &:before {
      margin-top: -12px; }

    &:after {
      margin-top: 12px; } }

  &--toggle {
    span {
      transition-duration: 0s;
      background-color: rgba($black, 0);
      transition-delay: 0.2s;

      &:before,
      &:after {
        transition-property: margin, transform;
        transition-duration: 0.2s;
        transition-delay: 0.2s, 0s;
        margin-top: 0; }

      &:before {
        transform: rotate(45deg); }

      &:after {
        transform: rotate(-45deg); } } } }

@media screen and ( max-width: 1366px) {
  .navigation {
    &__wrapper {
      width: 60%; }
    &__link {
      font-size: 1.25vw; } }

  .nav-submenu {
    &__link {
      font-size: 1.25vw; } } }

@media screen and ( max-width: 1224px) {
  .navigation {
    &__wrapper {
      width: 70%; }
    &__link {
      font-size: 1.4vw; } }

  .nav-submenu {
    &__link {
      font-size: 1.4vw; } } }

@media screen and (max-width: 1024px) {
  .navigation {
    width: 87.5%;
    &__wrapper {
      height: 90vh;
      width: 100%;
      position: fixed;
      top: 10vh;
      left: 0;
      padding: 10% 0;
      flex-direction: column;
      background-color: $white;
      transition: $transition;
      animation: hideMenu 1s ease forwards;
      &--open {
        display: flex;
        animation: showMenu 1s ease forwards;

        &:hover {
          .nav-submenu {
            display: none; } } } }

    &__item {
      height: calc(( 100% - 10vh ) / 6); }

    &__link {
      height: 100%;
      font-size: 3vw;
      padding: 2vh 0;
      color: $black;
      &:hover,
      &:focus,
      &:active {
        background-color: $purple; }
      &::after {
        display: none; } } }

  .nav-submenu {
    &__link {
      font-size: 1.75vw; } }

  .menu {
    display: flex; } }

@media screen and (max-width: 768px) {
  .navigation {
    &__wrapper {
      &--open {
        padding: 15% 0; } } }

  .logo {
    width: 20%; } }

@media screen and (max-width: 480px) {
  .navigation {
    &__wrapper {
      &--open {
        padding: 0 0 25%; } }

    &__link {
      font-size: 5vw;
      padding: 2rem 0; } }

  .logo {
    height: 5vh;
    width: 25%; } }

@media screen and (max-device-width: 767px) and (min-device-width: 320px) and (orientation: landscape) {
  .navigation {
    &__wrapper {
      &--open {
        padding: 2.5% 0; } } } }
