@import "./src/sass/helpers/var";

.pages {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 12.5rem 20% 12.5rem 0;
  opacity: 0.25;
  margin-left: 20%;

  &--wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    height: 50%;
    opacity: 0; }

  &__number {
    display: flex;
    width: 35%;
    justify-content: flex-start;
    align-items: center;
    text-align: left;
    font-size: 3.5vw;
    color: $purple;
    transition: $transition;
    position: relative; }

  &__title {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 4vw;
    padding-bottom: 1.5rem; }

  &__description {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: justify;
    justify-content: center;
    font-size: 1vw;
    line-height: 2.25rem;
    padding: 0 5% 0 15%;
    position: relative;
    &:before {
      content: '\1433';
      font-size: 2.5vw;
      display: block;
      width: 7.5%;
      height: 50%;
      position: absolute;
      top: 50%;
      left: 3.5%;
      transition: $transition;
      transform: translate(0, -50%); }

    &:hover {
      &:before {
        color: $purple; } } }

  &__img {
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      object-fit: contain;
      width: 80%; } } }

@media screen and (max-width: 1366px) {
  .pages {
    &__description {
      font-size: 1.25vw; }
    &__number {
      font-size: 4vw; } } }

@media screen and (max-width: 1024px) {
  .pages {
    &__title {
      font-size: 6.5vw; }
    &__description {
      font-size: 1.75vw;
      padding-right: 0; }
    &__number {
      width: 22.5%;
      font-size: 5vw; } } }

@media screen and ( max-width: 768px) {
  .pages {
    margin-left: 30%;
    &__title {
      font-size: 7vw; }
    &__description {
      font-size: 2.25vw;
      padding-right: 0; }
    &__number {
      font-size: 6vw; } } }

@media screen and ( max-width: 480px) {
  .pages {
    padding: 12.5rem 10% 12.5rem 0;
    margin-left: 25%;
    &__title {
      font-size: 7.5vw; }
    &__description {
      font-size: 3.25vw;
      line-height: 1.25rem;
      padding: 0;
      &::before {
        display: none; } }
    &__number {
      font-size: 8vw;
      width: 35%; } } }

@media screen and (max-device-width: 767px) and (min-device-width: 320px) and (orientation: landscape) {
  .pages {
    padding: 0 7.5% 0 0;
    margin-left: 17.5%; } }
