@import "./src/sass/helpers/var";

.hero {
    display: flex;
    width: 100%;
    position: relative;
    &__title {
        position: absolute;
        bottom: 0;
        left: 17.5%;
        transform: translateY(-50%);
        color: $white;
        font-size: 4vw; }

    &__img {
        width: inherit;
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover; } }

    .breadcrumbs {
        position: absolute;
        top: 10vh;
        left: 17.5%; } }

@media screen and ( max-width: 1224px) {
    .hero {
        &__title {
            left: 15%; }

        .breadcrumbs {
            left: 15%; } } }

@media screen and ( max-width: 1024px) {
    .hero {
        &__title {
            font-size: 4.5vw; } } }

@media screen and ( max-width: 768px) {
    .hero {
        min-height: 35vh;
        &__title {
            left: 7.5%; }

        .breadcrumbs {
            left: 7.5%; } } }

@media screen and ( max-width: 480px) {
    .hero {
        &__title {
            left: 10%;
            font-size: 8.5vw; } } }
