@import "./src/sass/helpers/var";

.spinner {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  overflow: hidden;

  &--wrapper {
    border: 2px dashed $purple;
    position: absolute;
    min-height: 50vh;
    width: 30%;
    left: 0;
    transform: translateX(-50%);
    margin: auto 0;
    border-radius: 50%; } }

@media screen and ( max-width: 1024px) {
  .spinner {
    &--wrapper {
      width: 35%; } } }

@media screen and (max-width: 768px) {
  .spinner {
    &--wrapper {
      width: 50%;
      min-height: 35vh; } } }

@media screen and (max-width: 480px) {
  .spinner {
    &--wrapper {
      width: 45%;
      min-height: 25vh; } } }
@media screen and (max-device-width: 767px) and (min-device-width: 320px) and (orientation: landscape) {
  .spinner {
    &--wrapper {
      width: 27.5%; } } }
