@import "helpers/reset";
@import "helpers/mixins";
@import "res/about";
@import "res/blog-box";
@import "res/btn";
@import "res/footer";
@import "res/header";
@import "res/link";
@import "res/modal";
@import "res/nav";
@import "res/our-projects";
@import "res/pages";
@import "res/project";
@import "res/services";
@import "res/services-box";
@import "res/social-menu";
@import "res/spinner";
@import "res/tabs";
@import "res/page";
@import "res/section";

@import 'res/services-content';
@import 'res/hero';

@keyframes showMenu {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%); }

  100% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0); } }

@keyframes hideMenu {
  0% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0); }

  100% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%); } }

*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: Montserrat;
  font-weight: 400;
  color: $black;
  display: flex;
  background-color: $gray;
  flex-direction: column;
  justify-content: space-between;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-margin-top: 10vh; }

.wrapper {
  position: relative;

  &--home {
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%; } }

.main {
  &-content {
    position: relative;
    width: 100%; }

  &--padding {
    padding: 0; } }

article {
  width: 100%;
  position: relative; }

svg {
  width: 100%;
  height: 100%; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 700; }

a {
  text-decoration: none;
  transition: $transition;
  width: 100%;
  color: $black; }

picture {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

p {
  line-height: 1.5rem;
  color: $darkgray; }

.container {
  display: flex;
  width: 65%;
  margin: 0 auto;
  flex-direction: column;
  position: relative;

  &--direction {
    flex-direction: row; } }

.scroll--hidden {
  overflow: hidden; }

.hide {
  display: none; }

.rotate {
  transform: translateX(20px); }

@keyframes show {
  from {
    opacity: 0;
    transform: translateY(5%);
    visibility: hidden; }
  to {
    opacity: 1;
    transform: translateY(0);
    visibility: visible; } }

@keyframes showPage {
  10% {
    transform: scale(1, 0.002); }
  35% {
    transform: scale(0.2, 0.002);
    opacity: 1; }
  50% {
    transform: scale(0.2, 0.002);
    opacity: 0; }
  85% {
    transform: scale(1, 0.002);
    opacity: 1; }
  100% {
    transform: scale(1, 1); } }

.title {
  &--wrapper {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    align-items: center; } }

.filter-number {
  margin-right: 0.25rem; }
.map {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  &--wrapper {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1; } }

.contacts {
  z-index: 5;
  background-color: rgba($white, 0.65);
  a,
  p {
    color: $black; } }

.breadcrumbs {
  display: flex;
  padding: 1rem 0;
  justify-content: flex-start;
  &__link {
    display: flex;
    .bold,
    .bold + span {
      color: $white;
      transition: $transition;
      &:hover,
      &:focus {
        color: $black; } }
    span {
      margin-right: 0.5rem;
      font-size: 1.1vw; } } }

.content--height {
  min-height: 100vh; }

.transition {
  display: flex;
  position: fixed;
  z-index: 9999;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  margin: 0;
  pointer-events: none;

  li {
    transform: scaleY(0);
    background-color: $purple;
    transform-origin: bottom left;
    height: 100%;
    width: 20%; } }

.img--wrapper {
  overflow: hidden;
  width: 100%;
  img {
    width: 100%;
    object-fit: cover; } }

.show {
  display: flex; }

.rotateBtn {
  svg {
    transition: $transition;
    transform: rotateX(180deg); } }

.mouse {
  display: none;
  width: 2.5rem;
  height: 3.5rem;
  border: 1px solid $purple;
  position: absolute;
  bottom: 15vh;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 20px;
  background-color: $white;
  &--wrapper {
    display: flex;
    width: 100%;
    height: 100%;
    position: relative; }

  &::before {
    display: block;
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
    width: 40%;
    height: 25%;
    border-radius: 50%;
    background-color: $purple;
    content: '';
    animation: roll 1.5s infinite; } }

@keyframes roll {
  0% {
    transform: translate(-50%, 0);
    opacity: 1; }
  100% {
    opacity: 0;
    transform: translate(-50%, 200%); } }

@media screen and (max-width: 1600px) {
  .container {
    width: 80%; } }

@media screen and ( max-width: 1366px) {
  .breadcrumbs {
    &__link {
      span {
        font-size: 1.25vw; } } } }

@media screen and (max-width: 1224px) {
  .container {
    width: 85%; } }
@media screen and (max-width: 1024px) {
  p {
    line-height: 1.25rem;
    font-size: 1vw; }
  .container {
    width: 85%; } }

@media screen and ( max-width: 768px) {
  .title {
    &--wrapper {
      justify-content: space-between; } }
  .breadcrumbs {
    opacity: 0;
    visibility: hidden;
    &__link {
      span {
        font-size: 2.25vw; } } } }

@media screen and ( max-width: 480px) {
  .breadcrumbs {
    &__link {
      span {
        font-size: 3.25vw; } } }

  .mouse {
    display: flex; } }

@media screen and (max-device-width: 767px) and (min-device-width: 320px) and (orientation: landscape) {
  .mouse {
    display: flex;
    bottom: 5vh; } }

@import "helpers/fonts";
