.menu span {
  margin: 0 auto;
  top: 12px;
  position: relative;
  width: 100%;
  height: 6px;
  background-color: #000000;
  display: block;
  transition: all 0.3s ease;
}

.menu span:before, .menu span:after {
  position: absolute;
  content: '';
  width: 100%;
  height: 6px;
  background-color: #000000;
  display: block;
  transition: all 0.3s ease;
}

