    font-size: 2.25vw;
  }
  .services-article__btn {
    width: 50%;
    padding: 0.75rem 1vw;
    font-size: 2.25vw;
  }
  .list-description__item {
    font-size: 2.25vw;
  }
}

@media screen and (max-width: 480px) {
  .services-content--wrapper {
    width: 95%;
    margin: 0 auto;
  }
  .services-content__legend {
    font-size: 4vw;
  }
  .services-article__title {
