.hero {
  display: flex;
  width: 100%;
  position: relative;
}

.hero__title {
  position: absolute;
  bottom: 0;
  left: 17.5%;
  transform: translateY(-50%);
  color: #ffffff;
  font-size: 4vw;
}

.hero__img {
  width: inherit;
  overflow: hidden;
}

.hero__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero .breadcrumbs {
  position: absolute;
  top: 10vh;
  left: 17.5%;
}

@media screen and (max-width: 1224px) {
  .hero__title {
    left: 15%;
  }
  .hero .breadcrumbs {
    left: 15%;
  }
}

@media screen and (max-width: 1024px) {
  .hero__title {
    font-size: 4.5vw;
  }
}

@media screen and (max-width: 768px) {
  .hero {
    min-height: 35vh;
  }
  .hero__title {
    left: 7.5%;
  }
  .hero .breadcrumbs {
    left: 7.5%;
  }
}

@media screen and (max-width: 480px) {
  .hero__title {
    left: 10%;
    font-size: 8.5vw;
  }
}

@keyframes showMenu {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}

@keyframes hideMenu {
  0% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
  100% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
  }
}

*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Montserrat;
  font-weight: 400;
  color: #000000;
  display: flex;
  background-color: #f9f9f9;
  flex-direction: column;
  justify-content: space-between;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-margin-top: 10vh;
}

.wrapper {
  position: relative;
}

.wrapper--home {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
}

.main-content {
  position: relative;
  width: 100%;
}

.main--padding {
  padding: 0;
}

article {
  width: 100%;
  position: relative;
}

svg {
  width: 100%;
  height: 100%;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
}

a {
  text-decoration: none;
  transition: all 0.3s ease;
  width: 100%;
  color: #000000;
}

picture {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

p {
  line-height: 1.5rem;
  color: #7d7d7d;
}

.container {
  display: flex;
  width: 65%;
  margin: 0 auto;
  flex-direction: column;
  position: relative;
}

.container--direction {
  flex-direction: row;
}

.scroll--hidden {
  overflow: hidden;
}

.hide {
  display: none;
}

.rotate {
  transform: translateX(20px);
}

@keyframes show {
  from {
    opacity: 0;
    transform: translateY(5%);
    visibility: hidden;
  }
  to {
    opacity: 1;
    transform: translateY(0);
    visibility: visible;
  }
}

@keyframes showPage {
  10% {
    transform: scale(1, 0.002);
  }
  35% {
    transform: scale(0.2, 0.002);
    opacity: 1;
  }
  50% {
    transform: scale(0.2, 0.002);
    opacity: 0;
  }
  85% {
    transform: scale(1, 0.002);
    opacity: 1;
  }
  100% {
    transform: scale(1, 1);
  }
}

.title--wrapper {
  display: flex;
  width: 100%;
  justify-content: flex-start;
  align-items: center;
}

.filter-number {
  margin-right: 0.25rem;
}

.map {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
}

.map--wrapper {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1;
}

.contacts {
  z-index: 5;
  background-color: rgba(255, 255, 255, 0.65);
}

.contacts a,
.contacts p {
  color: #000000;
}

.breadcrumbs {
  display: flex;
  padding: 1rem 0;
  justify-content: flex-start;
}

.breadcrumbs__link {
  display: flex;
}

.breadcrumbs__link .bold,
.breadcrumbs__link .bold + span {
  color: #ffffff;
  transition: all 0.3s ease;
}

.breadcrumbs__link .bold:hover, .breadcrumbs__link .bold:focus,
.breadcrumbs__link .bold + span:hover,
.breadcrumbs__link .bold + span:focus {
  color: #000000;
}

.breadcrumbs__link span {
  margin-right: 0.5rem;
  font-size: 1.1vw;
}

.content--height {
  min-height: 100vh;
}

.transition {
  display: flex;
  position: fixed;
  z-index: 9999;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  margin: 0;
  pointer-events: none;
}

.transition li {
  transform: scaleY(0);
  background-color: #b245de;
  transform-origin: bottom left;
  height: 100%;
  width: 20%;
}

.img--wrapper {
  overflow: hidden;
  width: 100%;
}

.img--wrapper img {
  width: 100%;
  object-fit: cover;
}

.show {
  display: flex;
}

.rotateBtn svg {
  transition: all 0.3s ease;
  transform: rotateX(180deg);
}

.mouse {
  display: none;
  width: 2.5rem;
  height: 3.5rem;
  border: 1px solid #b245de;
  position: absolute;
  bottom: 15vh;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 20px;
  background-color: #ffffff;
}

.mouse--wrapper {
  display: flex;
  width: 100%;
  height: 100%;
  position: relative;
}

.mouse::before {
  display: block;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  width: 40%;
  height: 25%;
  border-radius: 50%;
  background-color: #b245de;
  content: '';
  animation: roll 1.5s infinite;
}

@keyframes roll {
  0% {
    transform: translate(-50%, 0);
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translate(-50%, 200%);
  }
}

@media screen and (max-width: 1600px) {
  .container {
    width: 80%;
  }
}

@media screen and (max-width: 1366px) {
  .breadcrumbs__link span {
    font-size: 1.25vw;
  }
}

@media screen and (max-width: 1224px) {
  .container {
    width: 85%;
  }
}

@media screen and (max-width: 1024px) {
  p {
    line-height: 1.25rem;
    font-size: 1vw;
  }
  .container {
    width: 85%;
  }
}

@media screen and (max-width: 768px) {
  .title--wrapper {
    justify-content: space-between;
  }
  .breadcrumbs {
    opacity: 0;
    visibility: hidden;
  }
  .breadcrumbs__link span {
    font-size: 2.25vw;
  }
}

@media screen and (max-width: 480px) {
  .breadcrumbs__link span {
    font-size: 3.25vw;
  }
  .mouse {
    display: flex;
  }
}

@media screen and (max-device-width: 767px) and (min-device-width: 320px) and (orientation: landscape) {
  .mouse {
    display: flex;
    bottom: 5vh;
  }
}

